.header-container {
	.logo {
		height: 55px;
	}
	.top-header {
		height: 75px;
		padding: 1rem 1rem 1rem 0rem;
	}
	.navbar {
		background: #2e3192;
    height: 40;
		position: relative;
		padding: 0;

		.navbar-nav {
			.nav-link {
				float: left;
				border-right: 1px #1a1d72 solid; min-width: 150px;
				text-align: center;
				display: flex;
				align-items: center;
				justify-content: center;
				color: #fff;
				text-transform: uppercase;
				height: 40px;
				font-weight: bold;
				text-decoration: none;
				padding: 10px 20px 10px;
				&.active {
    			background: #f6f6f6;
					color: #2e3192;
					font-weight: bold;
				}
				&.active:hover {
					background: #f6f6f6;
					color: #2e3192;
				}
			}

			.nav-link:hover {
					background: #262662;
					color: #fff;
			}
		}
	}
	.login-user {
		float: right;
		padding: 1rem;
		margin-right: 20px;

		.caret-icon {
			font-size: 22px;
    	position: absolute;
    	margin-top: 2px;
    	margin-left: 2px;
		}
	}
}

