.art-unit-container {
  .card-box {
    min-height: 152px;
    display: flex;
    justify-content: center;
    align-content: center;
    flex-direction: column;
  }

  .card-title {
    font-size: 1.125rem;
  }

  .avg-time-frame {
    .title {
      font-size: 14px;
    }
    .card-box {
      min-height: 158px;
    }
  }

  .logo {
    height: 10px;
    opacity: 0;
  }

  .firm-column {
    width: 80%;
  }

  .columnHeader {
    min-height: 30px;
    display: flex;
    flex: 1;
    align-items: center;
  }

  .firms-table {
    height: 510px;
    overflow: auto;
  }
}
