.login-container {
	  width: 360px;
    position: absolute;
    top: 50%;
    left: 0;
    right: 0;
    margin: 0 auto;
    transform: translateY(-50%);
    border: 1px #f3f3f3 solid;
    box-shadow: 0 0 5px #ECECEC;
		padding: 25px;

		h2 {
    font-size: 22px;
    background: #2e3192;
    color: #fff;
    margin: -25px -25px 25px;
    padding: 15px 25px;
    border-radius: 3px 3px 0 0;
		}

		.login-logo {
			text-transform: uppercase;
			font-size: 16px;
			color: #2e3192;
			font-family: Raleway-SemiBold;
			text-align: center;
			border-bottom: 1px #e6e6e6 solid;
			padding-bottom: 15px;
		}

		.logo {
    	height: 55px;
		}

		.form-horizontal {
    	padding-top: 20px;
		}

		.control-label {
			font-size: 16px;
			font-weight: 600;
			margin-bottom: 0.5rem;
		}

		.forgot-pass-link {
			font-size: 16px;
			color: #2e3192;
			display: inline-block;
			margin-top: 5px;
		}
}