
.home-container {
	h1{
		font-size: 2.2rem;
		span {
			background: white;
    	padding: 12px;
		}
		&:before {
			border-top: 2px solid #dfdfdf;
			content: "";
			margin: 0 auto;
			position: absolute;
			top: 12.3rem;
			left: 0;
			right: 0;
			bottom: 0;
			width: 98%;
			z-index: -1;
		}
	}
	.block {
    background: #f3f3f3;
    border-radius: 5px;
    background: rgb(124, 226, 255);
    min-height: 120px;
		box-shadow: inset 0 0 60px #00c8ff;
		display: flex;


		a {
			padding: 1.5rem;
			text-decoration: none;
			color: #337ab7;
			text-transform: capitalize;
			h2 {
				font-size: 25px;
    		color: #007698;
    		text-shadow: 0 1px #fff;
			}
			p {
				color: #000000;
				text-transform: none;
			}
		}
	}
}


